import { useContext, Fragment,useState} from "react";
import { GlobalState } from "../../GlobalState";
import Menu from "./icon/menu.svg";
import Close from "./icon/delete.svg";

import { Link } from "react-router-dom";
import axios from "axios";

export default function Header() {
  const state = useContext(GlobalState);

  const [isLogged] = state.userAPI.isLogged;
  const [isAdmin] = state.userAPI.isAdmin;
  const [menu,setMenu] = useState(false)


  const logoutUser = async () => {
    await axios.get("/user/logout");
   localStorage.removeItem('firstLogin')  
    window.location.href = "/"; // TODO: PROBAR CON NAVIGATION DE REACT ROUTER DOM
  };

  const adminRouter = () => {
    return (
      <Fragment>
        <li>
          <Link to="/create_user">Usuarios</Link>
        </li>
      </Fragment>
    );
  };

  const loggedRouter = () => {
    return (
      <Fragment>
     <li>
          <Link to="/create_place">Crear Lugar</Link>
        </li>
        <li>
          <Link to="/create_event">Crear Evento</Link>
        </li>
        <li>
          <Link to="/" onClick={logoutUser}>
          Cerrar Sesin
          </Link>
        </li>
      </Fragment>
    );
  };

  const styleMenu = {
    left: menu ? 0 : "-100%"
  }

  return (
    <header>

      <div className="menu" onClick={() => setMenu(!menu)}>
        <img src={Menu} alt="" width="30" />
      </div>

      <div className="logo">
        <h1>
        <img src="https://res.cloudinary.com/djoinl8ht/image/upload/v1656547905/i4qyst6siygbchcp3iyt.png" alt="" height="50" width="50"/>
          <Link to="/">{isAdmin ? " Administrador" : " Pedro Moncayo Turstico"}

          </Link>
        </h1>
      </div>

      <ul style={styleMenu}>
      {isAdmin && adminRouter()}
        <li>
          <Link to="/">{"Lugares"}</Link>
        </li>
        <li>
          <Link to="/eventos">{"Eventos"}</Link>
        </li>
        

        {isLogged ? (
          loggedRouter()
        ) : (
          <li>
            <Link to="/login">LOGIN</Link>
          </li>
        )}

        <li onClick={() => setMenu(!menu)}>
          <img className="menu" src={Close} alt="" width="22" />
        </li>
      </ul>
    </header>
  );
}
