import { useContext, Fragment,useState} from "react";
import { GlobalState } from "../../../GlobalState";
import PlaceItem from "../Utils/PlaceItem/PlaceItem";
import Loading from "../Utils/Loading/Loading";
import toast, { Toaster } from 'react-hot-toast';
import axios from 'axios';
import Filters from "./Filters";
import LoadMore from "./LoadMore";

export default function Places() {
  const state = useContext(GlobalState);
  const [places,setPlaces] = state.placesAPI.places;
  const [isLogged] = state.userAPI.isLogged;
  const [token]= state.token
  const [callback,setCallback]=state.placesAPI.callback 
  const [loading,setLoading]= useState(false)
  const [isChecked,setIsChecked]=useState(false)

  const deletePlace = async (id)=> {

    try {
      setLoading(true)
    
      const deletePlace= await axios.delete(`/api/places/${id}`,{headers:{Authorization: token }})  
      await deletePlace
      setCallback(!callback)
      setLoading(false)
      toast.success(deletePlace.data.message)
    } catch (error) {
      toast.error(error.response.data.message)
    }
}


const handleCheck = (id)=> {
  // console.log(id)
  places.forEach(place => {
    if(place._id === id) place.checked = !place.checked
})
setPlaces([...places])
}


const checkAll = ()=>{
  places.forEach(place => { 
    place.checked =!isChecked
  })
  setPlaces([...places])
  setIsChecked(!isChecked)
}


const deleteAll =()=>{
  places.forEach(place => {
    if(place.checked) deletePlace(place._id)
  })
}



 if (loading) return <div ><Loading/></div>  // *Retorna el Loading

  return (
    <Fragment>
      <Filters/>
      {
        isLogged && 
        <div className="delete-all">
          <span>Seleccionar Todo</span>
          <input type="checkbox" checked={isChecked} onChange={checkAll}/>
          <button onClick={deleteAll}>Eliminar Todo</button>
        </div>
      }
      <div className="products">
        {places.map((place) => {
          return (
            
            <PlaceItem
              key={place._id}
              place={place}
              isLogged={isLogged}
              handleCheck={handleCheck}
              deletePlace={deletePlace}
            />
          );
        
        })}
      </div>
      <LoadMore/> 
      {places.length === 0 && <Loading />}
      <Toaster  position="top-right"  reverseOrder={false}/>   {/* // * Componente de Notificacion */}
    </Fragment>
  );
}
