import { useContext, useState, useEffect, Fragment} from "react";
import { useParams} from "react-router-dom";
import { GlobalState } from "../../../GlobalState";
import EventItem from "../Utils/EventItem/EventItem";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";

export default function DetailEvent() {
  const opciones = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric',timeZone: 'UTC' };
  const params = useParams();
  const state = useContext(GlobalState);
  const [events] = state.eventsAPI.events;
  const [detailEvent, setDetailEvent] = useState([]);

  useEffect(() => {
   
    if (params.id) {
        events.forEach(product => {
        if (product._id === params.id) setDetailEvent(product);
      });
    }
   
  }, [params.id, events]);
  if (detailEvent.length === 0) return null;

  return (

    <Fragment>

       <Carousel autoPlay={true} showArrows={false}>
         {detailEvent.images.map((imagen,i) =>(
           <div key={i} ><img src={imagen} alt="" />
             </div>
         ))}
      </Carousel>

      <div className="detail">
        <div className="box-detail">
          <div className="row">
            <h2>{detailEvent.title}</h2>
          </div>
          <span> <div className="title">Lugar del Evento</div> {detailEvent.lugar}</span>
          <span><div className="title">Hora para el Evento</div>  {detailEvent.hour}</span><br/>
          <span><div className="title">Fecha del Evento</div> {new Date(detailEvent.dateEvent).toLocaleDateString('es-ec',opciones)}</span>
        
          <div className="title">Descripcin del Evento</div> <p>{detailEvent.description}</p>
        </div>
      </div>

      <div>
        <h2>Eventos Relacionados</h2>
        <div className="events">
          {events.map(product => {
            return product.lugar === detailEvent.lugar ? (
              <EventItem key={product._id} event={product} />
            ) : null;
          })}
        </div>
      </div>
    </Fragment>
  );
}
