import React, { useContext } from "react";
import { GlobalState } from "../../../GlobalState";

export default function Filters() {
  const state = useContext(GlobalState);
  const [lugares] = state.lugaresAPI.lugares
  const [lugar, setLugar] = state.eventsAPI.lugar;
  const [sort, setSort] = state.eventsAPI.sort;
  const [search, setSearch] = state.eventsAPI.search;

  const handleCategory = (e) => {
        setLugar(e.target.value);
        setSearch('')
  };

  return (
    <div className="filter_menu">
      <div className="row">
        <span>Filtros: </span>
        <select name="category" value={lugar} onChange={handleCategory}>
          <option value="">Todos los Eventos</option>

          {lugares.map((lu,i) => (
            <option key={i} value={`lugar=${lu}`}>
              {lu}
            </option>
          ))}

        </select>
      </div>
      <input type="text" value={search} placeholder="Busca un evento !" onChange={(e) => setSearch(e.target.value.toLowerCase())}/>
      <div className="row sort">
        <span>Ordenar por: </span>
        <select value={sort} onChange={(e) => setSort(e.target.value)}>
          <option value="">Recientes</option>
          <option value="sort=oldest">Antiguos</option>
          <option value="sort=hour">Temprano</option>
          <option value="sort=-hour">Tarde</option>
    
        </select>
      </div>
    </div>
  );
}
