import { useState, useEffect } from "react";
import axios from "axios";
import toast from 'react-hot-toast';

export default function UsersAPI(token) {
  const [isLogged, setIsLogged] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);


 

  useEffect(() => {
    if (token) {
      const getUser = async () => {
        try {
          const res = await axios.get("/user/info", {headers: { Authorization: token }, //mando el token para que me permita hacer la consulta
          } );
          setIsLogged(true);
          res.data.user.role === "admin" ? setIsAdmin(true) : setIsAdmin(false);
        } catch (error) {
          toast.error(error.response.data.message)
        }
      };
      getUser();
    }
  }, [token]);

  


  return {
    isLogged: [isLogged, setIsLogged],
    isAdmin: [isAdmin, setIsAdmin],


  };
}
