import React,{useState} from 'react'
import toast, { Toaster } from 'react-hot-toast';
import axios from "axios"
import {BsFillPersonFill } from 'react-icons/bs';


export default function Login() {

  const [user,setUser]= useState({email:'',password:''})

  const onChangeInput = (e) => {
   const {name,value} = e.target
   setUser({...user,[name]:value})
  }

  const handleSubmit = async (e) => {
   e.preventDefault()
   try {
   await axios.post('/user/login',{...user})
    localStorage.setItem('firstLogin',true)
    //Notificacion loggin success
     toast.success("Inicio de session correcto")
     setTimeout(() =>{
      window.location.href ='/';
      // navigate('/') // TODO: Solucionar con navigate
     },1000)
    
     
   } catch (error) {
     
     //Notificacion de error en el login
     toast.error(error.response.data.message)
   }

  }

  return (
    <div className="login-page">
    <form onSubmit={handleSubmit}>
    <BsFillPersonFill style={{fontSize:"50px", color:"#093B78"}}/>
    <h2> Iniciar Sesin </h2>
      <input type="email" name="email" required placeholder="nombre@email.com" value={user.email} onChange={onChangeInput}/>
      <input type="password" name="password" required placeholder="contrasea" value={user.password} onChange={onChangeInput}/>
      <div className="row">
        <button className="loginsubmit" type='submit'> Login </button>
      </div>
    </form>
    <Toaster  position="top-right"  reverseOrder={false}/>
    
    </div>
  )
}

