import React, { useContext } from "react";
import { GlobalState } from "../../../GlobalState";

export default function Filters() {
  const state = useContext(GlobalState);
  const [categories] = state.CategoriesAPI.categories;
  const [category, setCategory] = state.placesAPI.category;
  const [sort, setSort] = state.placesAPI.sort;
  const [search, setSearch] = state.placesAPI.search;

  const handleCategory = (e) => {
        setCategory(e.target.value);
        setSearch('')
  };

  return (
    <div className="filter_menu">
      <div className="row">
        <span>Filtros: </span>
        <select name="category" value={category} onChange={handleCategory}>
          <option value="">Todos los Lugares</option>
          {categories.map((category) => (
            <option key={category._id} value={`category=${category.name}`}>
              {category.name}
            </option>
          ))}
        </select>
      </div>
      <input type="text" value={search} placeholder="Busca un lugar !" onChange={(e) => setSearch(e.target.value.toLowerCase())}/>
      <div className="row sort">
        <span>Ordenar por: </span>
        <select value={sort} onChange={(e) => setSort(e.target.value)}>
          <option value="">Recientes</option>
          <option value="sort=oldest">Mas Antiguo</option>
        </select>
      </div>
    </div>
  );
}
