import {useContext} from 'react'
import {  Routes, Route } from "react-router-dom";
import Login from './Auth/Login'
import Places from './Places/Places'
import Events from './Events/Events'
import DetailPlace from './DetailPlace/DetailPlace'
import DetailEvent from './DetailEvent/DetailEvent'
import Users from './Users/User'
import NotFound from './Utils/NoFound/NotFound'
import CreatePlace from './CreatePlace/CreatePlace'
import CreateEvent from './CreateEvent/CreateEvent'
import {GlobalState} from '../../GlobalState'


export default function Pages() {
  const state = useContext(GlobalState) 
  const [isLogged]= state.userAPI.isLogged
  const [isAdmin]= state.userAPI.isAdmin
  return (
    <Routes>
      <Route path="/" exact element={<Places/>} />
      <Route path="/eventos" exact element={<Events/>} />
      <Route path="/login" element={isLogged?<NotFound/>:<Login/>} />
      <Route path="/detailplace/:id" element={<DetailPlace/>} />
      <Route path="/detailevent/:id" element={<DetailEvent/>} />
      <Route path="/create_user" element={isAdmin?<Users/>:<NotFound/>} />
      <Route path="/create_place" element={isLogged?<CreatePlace/>:<NotFound/>} />
      <Route path="/edit_product/:id" element={isLogged?<CreatePlace/>:<NotFound/>} />
      <Route path="/create_event" element={isLogged?<CreateEvent/>:<NotFound/>} /> 
      <Route path="/edit_event/:id" element={isLogged?<CreateEvent/>:<NotFound/>} />
      <Route path="*" element={<NotFound/>} />
    </Routes>
  )
}
