import { useContext, useState, useEffect, Fragment} from "react";
import { useParams} from "react-router-dom";
import { GlobalState } from "../../../GlobalState";
import ProductItem from "../Utils/PlaceItem/PlaceItem";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";

export default function DetailProduct() {
  const params = useParams();
  const state = useContext(GlobalState);
  const [places] = state.placesAPI.places;
  const [detailPlace, setDetailPlace] = useState([]);



  useEffect(() => {
   
    if (params.id) {
        places.forEach(place => {
        if (place._id === params.id) setDetailPlace(place);
      });
     
    }
   
  }, [params.id, places]);


  if (detailPlace.length === 0) return null;

  return (
    
    <Fragment>

       <Carousel autoPlay={true} showArrows={false}>
         {detailPlace.images.map((imagen,i) =>(
           <div key={i} ><img src={imagen} alt="" />
           
             </div>
         ))}
      </Carousel>



      <div className="detail">
        <div className="box-detail">
          <div className="row">
            <h2>{detailPlace.title}</h2>
          </div>
          <span> <div className="title">Categora</div>{detailPlace.category}</span>
          <span><div className="title">Parroquia</div>{detailPlace.parroquia}</span><br/>
          <span><div className="title">Ubicacin</div>{`Latitud: ${detailPlace.lat} - Longitud: ${detailPlace.lng}`}</span>
          <div className="title">Descripcin</div> <p>{detailPlace.description}</p>
          <span><div className="title">Contacto</div> {detailPlace.contact}</span>
        </div>
      </div>

      <div>
        <h2>Lugares Relacionados</h2>
        <div className="places">
          {places.map(place => {
            return place.category === detailPlace.category ? (
              <ProductItem key={place._id} place={place} />
            ) : null;
          })}
        </div>
      </div>
    </Fragment>
  );
}
