import {useContext} from 'react'
import { Link } from "react-router-dom";
import {GlobalState} from "../../../../GlobalState"


export default function BtnRender({event,deleteEvent}) {

  const state = useContext(GlobalState);
  const [isLogged]= state.userAPI.isLogged


  return (
     <div className="row_btn">
       {
         isLogged ? 
         <>
         <Link id="btn_delete" to="#!" onClick={()=>deleteEvent(event._id)}>Eliminar</Link>
         <Link id="btn_view_log" to={`/edit_event/${event._id}`}>Editar</Link>
         </>
         :
         <>
         <Link id="btn_view" to={`/detailevent/${event._id}`}>Ver Evento</Link>
         </>
         
       }
         
      </div> 
  )
}

