import {useContext} from 'react'
import { Link } from "react-router-dom";
import {GlobalState} from "../../../../GlobalState"


export default function BtnRender({place,deletePlace}) {

  const state = useContext(GlobalState);
  const [isLogged]=state.userAPI.isLogged


  return (
     <div className="row_btn">
       {
         isLogged ? 
         <>
         <Link id="btn_delete" to="#!" onClick={()=>deletePlace(place._id)}>Eliminar</Link>
         <Link id="btn_view_log" to={`/edit_product/${place._id}`}>Editar</Link>
         </>
         :
         <>
         <Link id="btn_view" to={`/detailplace/${place._id}`}>Ver Lugar</Link>
         </>
         
       }
         
      </div> 
  )
}

