import React, { useState, useContext, useEffect, useRef } from "react";
import axios from "axios";
import { GlobalState } from "../../../GlobalState";
import Loading from "../Utils/Loading/Loading";
import { useNavigate, useParams } from "react-router-dom";
import toast, { Toaster } from "react-hot-toast";
import { BsImages } from "react-icons/bs";
import { Wrapper } from "@googlemaps/react-wrapper";
import useDeepCompareEffect from "use-deep-compare-effect";

const render = (status) => {
  return <h1>{status}</h1>;
};

const Map = ({ onClick, onIdle, children, style, ...options }) => {
  const ref = useRef(null);
  const [map, setMap] = useState();

  useEffect(() => {
    if (ref.current && !map) {
      setMap(new window.google.maps.Map(ref.current, {}));
    }
  }, [ref, map]);

  useDeepCompareEffect(() => {
    if (map) {
      map.setOptions(options);
    }
  }, [map, options]);

  useEffect(() => {
    if (map) {
      ["click", "idle"].forEach((eventName) => window.google.maps.event.clearListeners(map, eventName));

      if (onClick) {
        map.addListener("click", onClick);
      }

      if (onIdle) {
        map.addListener("idle", () => onIdle(map));
      }
    }
  }, [map, onClick, onIdle]);

  return (
    <>
      <div ref={ref} style={style} />
      {React.Children.map(children, (child) => {
        if (React.isValidElement(child)) {
          return React.cloneElement(child, { map });
        }
      })}
    </>
  );
};

const Marker = (options) => {
  const [marker, setMarker] = useState();

  useEffect(() => {
    if (!marker) {
      setMarker(new window.google.maps.Marker());
    }

    return () => {
      if (marker) {
        marker.setMap(null);
      }
    };
  }, [marker]);

  useEffect(() => {
    if (marker) {
      marker.setOptions(options);
    }
  }, [marker, options]);

  return null;
};

const initialState = {
  title: "",
  description: "",
  parroquia: "",
  category: "",
  images: [],
  lat: "",
  lng: "",
  zoom: "",
  contact: "",
  _id: "",
};

export default function CreatePlace() {
  const [zoom, setZoom] = useState(12); // initial zoom
  const [center, setCenter] = useState({ lat: 0.04777, lng: -78.22168 });
  const [valor, setValor] = useState({ lat: "", lng: "" });
  const [clicks, setClicks] = useState([]);

  const onClick = (e) => {
    setClicks([e.latLng]);
    setValor(e.latLng.toJSON());
  };

  const onIdle = (m) => {
    setZoom(m.getZoom());
    setCenter(m.getCenter().toJSON());
  };

  const state = useContext(GlobalState);
  const [place, setPlace] = useState(initialState);
  const [categories] = state.CategoriesAPI.categories;
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [token] = state.token;
  const navigate = useNavigate();
  const param = useParams();
  const [products] = state.placesAPI.places;
  const [onEdit, setOnEdit] = useState(false);
  const [check, setCheck] = useState(false);
  const [checkmap, setCheckmap] = useState(false);
  const [callback, setCallback] = state.placesAPI.callback;

  useEffect(() => {
    if (param.id) {
      setOnEdit(true);

      products.forEach((place) => {
        if (place._id === param.id) {
          setPlace(place);
          setFile(null);
        }
      }
      );
     
    } else {
      setOnEdit(false);
      setPlace(initialState);
    }
  }, [param.id, products]);

  const handleChangeInput = (e) => {
    const { name, value } = e.target;
    setPlace({ ...place, [name]: value });
  };

  const handleCheck = () => {
    setCheck(!check);
    
  };

  const handleCheckmap = () => {
    setCheckmap(!checkmap);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (onEdit) {
        if (file) {
          const formData = new FormData();
          for (let i = 0; i < file.length; i++) {
            formData.append("file", file[i]);
          }
          formData.append("title", place.title);
          formData.append("description", place.description);
          formData.append("parroquia", place.parroquia);
          formData.append("category", place.category);
          checkmap ? formData.append("lat", valor.lat) : formData.append("lat", place.lat);
           checkmap ? formData.append("lng", valor.lng) : formData.append("lng", place.lng);
          formData.append("zoom", zoom);
          formData.append("contact", place.contact);
          setLoading(true);
          const res = await axios.put(`/api/places/${place._id}`, formData, {
            headers: { "content-type": "multipart/form-data", Authorization: token },
          });
          toast.success(res.data.message);
        } else {
          setLoading(true);
       
          const res = await axios.put(
            `/api/places/${place._id}`,
            { ...place, lat: checkmap ? valor.lat:place.lat, lng: checkmap ? valor.lng:place.lng, zoom },
            { headers: { Authorization: token } }
          );
          toast.success(res.data.message);
        }
      } else {
        if (clicks.length > 0) {
          if (file) {
            const formData = new FormData();
            for (let i = 0; i < file.length; i++) {
              formData.append("file", file[i]);
            }
            formData.append("title", place.title);
            formData.append("description", place.description);
            formData.append("parroquia", place.parroquia);
            formData.append("category", place.category);
            formData.append("lat", valor.lat);
            formData.append("lng", valor.lng);
            formData.append("zoom", zoom);
            formData.append("contact", place.contact);
            setLoading(true);
            const res = await axios.post("/api/places", formData, {
              headers: { "content-type": "multipart/form-data", Authorization: token },
            });
            setLoading(false);
            toast.success(res.data.message);
          } else {
            return toast.error("Debe cargar una o varias imgenes");
          }
        } else {
          return toast.error("Seleccionar una ubicacin en el mapa");
        }
      }
      setCallback(!callback);
   
      setPlace(initialState);
      navigate("/"); // * navegamos a inicio
    } catch (err) {
      toast.error(err.response.data.message);
    }
  };

  return (
    <div className="create_product">
      {(!onEdit || checkmap) && (
        <div className="upload">
          <Wrapper apiKey={"AIzaSyCWFupoeZD2JJMcPcrev5T5MmxVsSYB2wA"} render={render}>
            <Map center={center} onIdle={onIdle} onClick={onClick} style={{ width: "100%", height: "100%" }} zoom={zoom}>
              {clicks.map((latLng, i) => (
                <Marker key={i} position={latLng} />
              ))}
            </Map>
          </Wrapper>
        </div>
      )}

      <form onSubmit={handleSubmit}>
        {/* MAPA INPUTS  */}
        <input type="hidden" id="zoom" name="zoom" value={zoom} disabled onChange={(event) => setZoom(Number(event.target.value))} />
        <br />
        {onEdit && !checkmap ? (
          <div className="rowchecked">
            <input type="checkbox" name="my-checkbox" id="opt-in" checked={checkmap || false} onChange={handleCheckmap} />
            <label htmlFor="opt-in">Cargar nuevas coordenadas</label>
          </div>
        ) : (
          ""
        )}

        {checkmap || clicks.length > 0 ? (
          <>
            <div className="row">
              <label htmlFor="lat">Latitud</label>
              <input
                type="number"
                id="lat"
                name="lat"
                value={valor.lat}
                disabled
                onChange={(event) => setValor({ lat: Number(event.target.value) })}
              />
            </div>
            <div className="row">
              <label htmlFor="lng">Longitud</label>
              <input
                type="number"
                id="lng"
                name="lng"
                value={valor.lng}
                disabled
                onChange={(event) => setValor({ lng: Number(event.target.value) })}
              />
            </div>
          </>
        ) : (
          ""
        )}

        {onEdit && (
          <div className="rowchecked">
            <input type="checkbox" name="my-checkbox" id="opt-in" checked={check || false} onChange={handleCheck} />
            <label htmlFor="opt-in">Cargar nuevas fotos</label>
          </div>
        )}

        {(!onEdit || check) && (
          <div className="row">
            <label htmlFor="fichero" className="circle">
              {" "}
              <BsImages style={{ fontSize: "45px" }} />
              Cargar Imgenes
            </label>
            <input id="fichero" type="file" onChange={(e) => setFile(e.target.files)} accept="image/*" multiple />
          </div>
        )}
        <div className="row">
          <label htmlFor="title">Ttulo</label>
          <input
            type="text"
            name="title"
            placeholder="Ingrese ttulo del lugar"
            id="title"
            required
            value={place.title}
            onChange={handleChangeInput}
          />
        </div>

        <div className="row">
          <label htmlFor="description">Descripcin</label>
          <textarea
            type="text"
            name="description"
            id="description"
            placeholder="Escriba una breve descripcin......."
            required
            value={place.description}
            rows="5"
            onChange={handleChangeInput}
          />
        </div>

        <div className="row">
          <label htmlFor="parroquia">Parroquia</label>
          <input
            type="text"
            name="parroquia"
            placeholder="Parroquia ..."
            id="parroquia"
            required
            value={place.parroquia}
            onChange={handleChangeInput}
          />
        </div>

        <div className="row">
          <label htmlFor="contact">Contacto</label>
          <input
            type="text"
            name="contact"
            id="contact"
            required
            placeholder="0987654321 - 22366543"
            value={place.contact}
            onChange={handleChangeInput}
          />
        </div>

        <div className="row">
          <label htmlFor="categories">Categorias: </label>
          <select name="category" value={place.category} onChange={handleChangeInput} required>
            <option value="">Seleccione una Categora</option>
            {categories.map((category) => (
              <option value={category.name} key={category._id}>
                {category.name}
              </option>
            ))}
          </select>
        </div>
        {loading ? <Loading /> : <button type="submit">{onEdit ? "Actualizar" : "Crear"}</button>}
      </form>
      <Toaster position="top-right" reverseOrder={false} />
    </div>
  );
}
