import { useContext, Fragment,useState} from "react";
import { GlobalState } from "../../../GlobalState";
import EventItem from "../Utils/EventItem/EventItem";
import Loading from "../Utils/Loading/Loading";
import toast, { Toaster } from 'react-hot-toast';
import axios from 'axios';
import Filters from "./Filters";
import LoadMore from "./LoadMore";

export default function Products() {
  const state = useContext(GlobalState);
  const [events, setEvents]= state.eventsAPI.events;
  const [isLogged] = state.userAPI.isLogged;
  const [token]= state.token
  const [callback,setCallback]=state.eventsAPI.callback 
  const [loading,setLoading]= useState(false)
  const [isChecked,setIsChecked]=useState(false)

  const deleteEvent = async (id)=> {

    try {
      setLoading(true)
      
      const deleteEvent= await axios.delete(`/api/events/${id}`,{headers:{Authorization: token }})  
      await deleteEvent
      setCallback(!callback)
      setLoading(false)
      toast.success(deleteEvent.data.message)
    } catch (error) {
      toast.error(error.response.data.message)
    }
}

const handleCheck = (id)=> {
  // console.log(id)
  events.forEach(event => {
    if(event._id === id) event.checked = !event.checked
})
setEvents([...events])
}


const checkAll = ()=>{
  events.forEach(event => { 
    event.checked =!isChecked
  })
  setEvents([...events])
  setIsChecked(!isChecked)
}


const deleteAll =()=>{
  events.forEach(event => {
    if(event.checked) deleteEvent(event._id)
  })
}

 if (loading) return <div ><Loading/></div>  // *Retorna el Loading

  return (
    <Fragment>
      <Filters/>
      {
        isLogged && 
        <div className="delete-all">
          <span>Seleccionar Todo</span>
          <input type="checkbox" checked={isChecked} onChange={checkAll}/>
          <button onClick={deleteAll}>Eliminar Todo</button>
        </div>
      }
      <div className="products">
        {events.map((event) => {
          return (
            
            <EventItem
              key={event._id}
              event={event}
              isLogged={isLogged}
              handleCheck={handleCheck}
              deleteEvent={deleteEvent}
            />
          );
        
        })}
      </div>
      <LoadMore/> 
      {events.length === 0 && <Loading />}
      <Toaster  position="top-right"  reverseOrder={false}/>   {/* // * Componente de Notificacion */}
    </Fragment>
  );
}
