import React, { useState, useContext, useEffect } from "react";
import axios from "axios";
import { GlobalState } from "../../../GlobalState";
import Loading from "../Utils/Loading/Loading";
import { useNavigate, useParams } from "react-router-dom";
import toast, { Toaster } from "react-hot-toast";
import { BsImages } from 'react-icons/bs';
import moment from 'moment';

const initialState = {
  title: "",
  description: "",
  images: [],
  lugar: "",
  dateEvent: "",
  hour: "",
  _id: "",
};

export default function CreateEvent() {
  const state = useContext(GlobalState);
  const [event, setEvent] = useState(initialState);
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [token] = state.token;
  const navigate = useNavigate();
  const param = useParams();
  const [events] = state.eventsAPI.events;
  const [onEdit, setOnEdit] = useState(false);
  const [check, setCheck] = useState(false);
  const [callback, setCallback] = state.eventsAPI.callback;

  useEffect(() => {
    if (param.id) {
      setOnEdit(true);

      events.forEach((event) => {
        if (event._id === param.id) {
          setEvent(event);
          setFile(null);
        }
      });
    } else {
      setOnEdit(false);
      setEvent(initialState);
    }
  }, [param.id, events]);

  const handleChangeInput = (e) => {
    const { name, value } = e.target;
    setEvent({ ...event, [name]: value });
    console.log([name], value);

  };
 
  const handleChangeInputDate = (e) => {
  // const options= { year: 'numeric', month: 'numeric', day: 'numeric',timeZone: 'UTC' };
  // new Date(dateValue).toLocaleDateString('es-ec',options) //transformamos la fecha y validamos para que no reste 1 dia
  const dateValue= e.target.value;
  const dateFormat= moment(dateValue).utc().format('YYYY-MM-DD')

  setEvent({ ...event, dateEvent:dateFormat });

  }

const handleChangeInputHour = (e) => {
  const hourInput= e.target.value;
  setEvent({...event,hour: hourInput})
}

  const handleCheck = () => {
    setCheck(!check);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (onEdit) {
        if (file) {
          const formData = new FormData();
          for (let i = 0; i < file.length; i++) {
            formData.append("file", file[i]);
          }
          formData.append("title", event.title);
          formData.append("description", event.description);
          formData.append("lugar", event.lugar);
          formData.append("dateEvent",event.dateEvent);
          formData.append("hour", event.hour);
          
          setLoading(true);
          const res = await axios.put(`/api/events/${event._id}`, formData, { headers: {"content-type": "multipart/form-data", Authorization: token } });
          toast.success(res.data.message);
        } else {
          setLoading(true);
          const res = await axios.put(`/api/events/${event._id}`, { ...event }, { headers: { Authorization: token } });
          toast.success(res.data.message);
        }
      } else {
        if (file) {
          const formData = new FormData();
          for (let i = 0; i < file.length; i++) {
            formData.append("file", file[i]);
          }
          formData.append("title", event.title);
          formData.append("description", event.description);
          formData.append("lugar", event.lugar);
          formData.append("dateEvent",event.dateEvent);
          formData.append("hour", event.hour);
          setLoading(true);
          const res = await axios.post("/api/events", formData, {
            headers: { "content-type": "multipart/form-data", Authorization: token },
          });
          setLoading(false);
          toast.success(res.data.message);
        } else {
          return toast.error("Debe cargar una o varias imagenes");
        }
      }
      setCallback(!callback);
      setEvent(initialState);
      navigate("/eventos"); // * navegamos a inicio
    } catch (err) {
      toast.error(err.response.data.message);
    }
  };

  return (
    <div className="create_product">
      <form onSubmit={handleSubmit}>
        {onEdit && (
          <div className="rowchecked">
            <input type="checkbox" name="my-checkbox" id="opt-in" checked={check || false} onChange={handleCheck} />
            <label htmlFor="opt-in">Cargar nuevas fotos</label>
          </div>
        )}

        {(!onEdit || check) && (
          <div className="row">
           <label htmlFor="fichero" className="circle"> <BsImages style={{ fontSize: '45px'}}/>Cargar Imgenes</label>
            <input
              id="fichero" type="file"
              onChange={(e) => setFile(e.target.files)}
              accept="image/*"
              multiple
            />
          </div>
        )}

        <div className="row">
          <label htmlFor="title">Ttulo del Evento</label>
          <input
            type="text"
            name="title"
            placeholder="Ingrese ttulo"
            id="title"
            required
            value={event.title}
            onChange={handleChangeInput}
          />
        </div>

        <div className="row">
          <label htmlFor="description">Descripcin del Evento</label>
          <textarea
            type="text"
            name="description"
            id="description"
            placeholder="Breve Descripcin ......"
            required
            value={event.description}
            rows="5"
            onChange={handleChangeInput}
          />
        </div>

        <div className="row">
          <label htmlFor="lugar">Lugar</label>
          <input
            type="text"
            name="lugar"
            placeholder="Ingrese el lugar del evento ..."
            id="lugar"
            required
            value={event.lugar}
            onChange={handleChangeInput}
          />
        </div>
     
        <div className="row">
          <label htmlFor="fecha">Fecha del Evento</label>
          <input
            type="date"
            name="fecha"
            id="fecha"
            required
            placeholder="Seleccione una fecha"
            value={!onEdit? event.dateEvent: moment(event.dateEvent).utc().format('YYYY-MM-DD')}  //usamos moment para poder formatear la hora
            onChange={(e)=>handleChangeInputDate(e)}
          />
        </div>


        <div className="row">
          <label htmlFor="hora">Hora del Evento</label>
          <input type="time" name="hora" id="hora" required value={event.hour} onChange={(e)=>handleChangeInputHour(e)} />
        </div>

        {loading ? <Loading /> : <button type="submit">{onEdit ? "Actualizar" : "Crear"}</button>}
      </form>
      <Toaster position="top-right" reverseOrder={false} />
    </div>
  );
}
