import React, {useEffect,useState,useRef} from 'react'
import BtnRender from './BtnRender'
import { motion } from 'framer-motion'

export default function PlaceItem({place,isLogged,deletePlace,handleCheck}) {

const [width,setWidth] = useState(0)
const carousel = useRef()

useEffect(() => {
  setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth);
  // console.log("carouselDentro",carousel)
},[])

  return (
    
    <div className="product_card">
      {
        isLogged && <input type="checkbox" checked={place.checked} onChange={()=>handleCheck(place._id)}/>
      } 
     {/* Carrusel de Imagenes */}
      <motion.div ref={carousel} className='slider-container'>
        <motion.div className='slider' drag='x' dragConstraints={{ right:0,left:-width }} >
        { place.images.map((image,i) => (
            <motion.div className='item' key={i}>
                <img  src={image} alt="" />
            </motion.div>
        ))}
        </motion.div>
    </motion.div>
  
      <div className="product_box"> 
       <h2 title={place.title}>{place.title}</h2>
       <span>{place.category}</span>
       <p>{place.description}</p>
       </div>
      <BtnRender place={place} deletePlace={deletePlace}/>

    </div>
  )
}

