import React, {useEffect,useState,useRef} from 'react'
import BtnRender from './BtnRender'
import { motion } from 'framer-motion'

export default function EventItem({event,isLogged,deleteEvent,handleCheck}) {

const [width,setWidth] = useState(0)
const carousel = useRef()

useEffect(() => {
  setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth);
},[])

  return (
    
    <div className="product_card">
      {
        isLogged && <input type="checkbox" checked={event.checked} onChange={()=>handleCheck(event._id)}/>
      } 
     {/* Carrusel de Imagenes */}
      <motion.div ref={carousel} className='slider-container'>
        <motion.div className='slider' drag='x' dragConstraints={{ right:0,left:-width }} >
        { event.images.map((image,i) => (
            <motion.div className='item' key={i}>
                <img  src={image} alt="" />
            </motion.div>
        ))}
        </motion.div>
    </motion.div>
  
      <div className="product_box">
       <h2 title={event.title}>{event.title}</h2>
       <span>Lugar:{event.lugar}</span>
       <p>{event.description}</p>
       </div>
      <BtnRender event={event} deleteEvent={deleteEvent}/>

    </div>
  )
}

