import React, { useState, useContext } from "react";
import axios from "axios";
import { GlobalState } from "../../../GlobalState";
import toast, { Toaster } from "react-hot-toast";

export default function User() {
  const state = useContext(GlobalState);
  const initialState = { name: "", email: "", password: "" };
  const [users] = state.listUsersAPI.users;
  const [callback, setCallback] = state.listUsersAPI.callback;
  const [token] = state.token;
  const [onEdit, setOnEdit] = useState(false);
  const [check, setCheck] = useState(false);
  const [id, setId] = useState("");
  const [user, setUser] = useState(initialState);

  const onChangeInput = (e) => {
    const { name, value } = e.target;
    setUser({ ...user, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (onEdit) {
        const res = await axios.put(`/user/update/${id}`, { ...user }, { headers: { Authorization: token } });
        toast.success(res.data.message);
      } else {
        const res = await axios.post("/user/register", { ...user }, { headers: { Authorization: token } });
        toast.success(res.data.message);
      }
      setOnEdit(false);
      setUser(initialState);
      setCallback(!callback);
    } catch (error) {
      toast.error(error.response.data.message);
    }

  };

  const editUser = async (id, user) => {
    setId(id);
    setOnEdit(true);
    setUser({ ...user,password:"" });
  };
  
  const deleteUser = async (id) => {
    try {
      const res = await axios.delete(`/user/delete/${id}`, {
        headers: { Authorization: token },
      });
      setCallback(!callback);
      toast.success(res.data.message);
    } catch (error) {
      //  alert(error.response.data.message);
      toast.error(error.response.data.message);
    }
  };

  const handleCheck = () => {
    setCheck(!check);
    console.log(check);
  };

  return (
    <>
      {/* FORMULARIO DE REGISTRO DE UN NUEVO USUARIO */}
      <div className="register-page">
        <form onSubmit={handleSubmit}>
          <h2>{onEdit ? "Actualizar Usuario" : "Registrar Un Nuevo Usuario"}</h2>
          <input
            type="text"
            name="name"
            required
            placeholder="Nombre de Usuario"
            value={user.name}
            onChange={onChangeInput}
          />
          <input type="email" name="email" required placeholder="Email" value={user.email} onChange={onChangeInput} />

          {onEdit && (
            <div className="rowchecked">
              <input type="checkbox" name="my-checkbox" id="opt-in" checked={check} onChange={handleCheck} />
              <label htmlFor="opt-in">Cambiar la Contrasea!</label>
            </div>
          )}

          {(!onEdit || check) && (
            <input
              type="password"
              name="password"
              placeholder={onEdit ? "Puedes cambiar de contrasea" : "Contrasea"}
              autoComplete="on"
              value={user.password}
              onChange={onChangeInput}
            />
          )}

          <div className="row">
            <button type="submit">{onEdit ? "Actualizar" : "Registrar"} </button>
          </div>
        </form>
        <Toaster position="top-right" reverseOrder={false} /> {/* // * Componente de Notificacion */}
      </div>

      {/*  TABLA DE USUARIOS */}
      <div className="history-page">
        <h2>Usuarios</h2>
        <table>
          <thead>
            <tr>
              <th>Usuario</th>
              <th>Correo</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user) => (
              <tr key={user._id}>
                <td>{user.name}</td>
                <td>{user.email}</td>
                <td>
                  <button className="btnedit" onClick={() => editUser(user._id, user)}>
                    {" "}
                    Edit{" "}
                  </button>
                  {user.role !== "admin" && (
                    <button
                      className="btndelete"
                      onClick={() => {
                        deleteUser(user._id);
                      }}
                    >
                      Delete
                    </button>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  );
}
